@import url("https://fonts.googleapis.com/css2?family=Pinyon+Script&display=swap");
@import url('https://fonts.googleapis.com/css2?family=Inter:ital,opsz,wght@0,14..32,100..900;1,14..32,100..900&display=swap');
@import url("https://fonts.googleapis.com/css2?family=STIX+Two+Text:ital,wght@0,400..700;1,400..700&display=swap");

h1 {
    margin: 0px;
    padding: 0px;
    margin-block: 0;
    font-family: "Pinyon Script", cursive;
    font-size: 50px;
    font-weight: 400;
    font-style: normal;
    color: #9c4d95;
}

h2 {
    padding: 0;
    margin-block: 0;
    font-family: "STIX Two Text", serif;
    font-size: 23px;;
    font-weight: 400;
    font-style: normal;
    color: #333333;
}

p {
    font-family: "Inter";
    font-size: 12px;
    font-weight: 400;
    color: #333333;
}

b {
    color: #9c4d95;
}

.header {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 5px;
}

.projects-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    height: 100%;
    overflow: hidden;
}

.header-sticky {
    position: sticky;
    top: 0;
    width: 100%;
    background-color: white;
    margin-top: 20px;
    padding-top: 20px;
    padding-bottom: 20px;
    z-index: 2;
}

.project-notes {
    display: flex;
    justify-content: center;
    flex-direction: column;
    margin-bottom: 50px;
}

.project-notes-row img {
    width: 400px;
}

.sticky-note {
    position: relative;
    display: inline-block;
    opacity: 0;
    transform: translateY(40px);
    transition: transform 0.2s ease;
}

.fade-in-up {
    opacity: 1;
    transform: translateY(0);
    animation: fadeInUp 0.8s cubic-bezier(0.23, 1, 0.32, 1);
}

@keyframes fadeInUp {
    0% {
        opacity: 0;
        transform: translateY(40px);
    }
    100% {
        opacity: 1;
        transform: translateY(0);
    }
}

.sticky-note:hover {
    transform: scale(1.08);
}

.project-notes-row {
    display: flex;
    flex-direction: row;
    justify-content: center;
    padding-bottom: 30px;
    gap: 60px;
}

.sticky-note .show-grow1 {
    position: absolute;
    top: -10px;
    right: -10px;
    width: 120px;
    height: 120px;
    opacity: 0;
    transition: opacity 0.2s ease;
    pointer-events: none;
    z-index: 2;
}

.sticky-note:hover .show-grow1 {
    opacity: 1;
}

.sticky-note .show-grow2 {
    position: absolute;
    bottom: -10px;
    left: -10px;
    width: 140px;
    opacity: 0;
    transition: opacity 0.2s ease;
    pointer-events: none;
    z-index: 2;
}

.sticky-note:hover .show-grow2 {
    opacity: 1;
}

.sticky-note-text {
    position: absolute;
    font-size: 2vw;
    font-weight: bold;
    color: #222;
    text-align: center;
    pointer-events: none;
    z-index: 3;
}

.note-1 {
    top: 48%;
    left: 48%;
    transform: translate(-50%, -50%) rotate(-3deg);
}

.note-2 {
    top: 49%;
    left: 50%;
    transform: translate(-50%, -50%) rotate(2deg);
}

.note-3 {
    top: 53%;
    left: 50%;
    transform: translate(-50%, -50%) rotate(2deg);
}

.note-4 {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) rotate(0deg);
}

.note-5 {
    top: 53%;
    left: 50%;
    transform: translate(-50%, -50%) rotate(-1deg);
}
